import React, { useState } from 'react'
import { useHistory, NavLink } from 'react-router-dom'
import {
  AppBar,
  Toolbar,
  Typography,
  IconButton,
  Menu,
  MenuItem
} from '@material-ui/core'
import MenuIcon from '@mui/icons-material/Menu'
import { makeStyles } from '@material-ui/core/styles'
import useMediaQuery from '@mui/material/useMediaQuery'

const useStyles = makeStyles((theme) => ({
  rootNav: {
    width: '100%',
    backgroundColor: '#03143a'
  },
  menuButton: {
    flexGrow: 0
  },
  menuList: {
    '& a.active': {
      fontWeight: 'bold',
      textDecoration: 'underline'
    },
    display: 'flex',
    flexGrow: 1
  },
  title: {
    flexGrow: 1
  }
}))

export const NavbarComponent = () => {
  const [anchorEl, setAnchorEl] = useState(null)
  const classes = useStyles()
  const history = useHistory()
  const matches = useMediaQuery('(max-width:768px)')
  const open = Boolean(anchorEl)
  const handleClick = (event) => {
    setAnchorEl(event.currentTarget)
  }
  const handleMenuClick = (pageURL) => {
    history.push(pageURL)
    setAnchorEl(null)
  }
  return (
    <AppBar position='static' className={classes.rootNav}>
      <Toolbar>
        <Typography variant='h5' className={classes.title} component='h2'>
          Портфолио
        </Typography>
        <Menu
          id='menu-appbar'
          anchorEl={anchorEl}
          anchorOrigin={{
            vertical: 'top',
            horizontal: 'right'
          }}
          open={open}
          onClose={() => setAnchorEl(null)}
        >
          <MenuItem onClick={() => handleMenuClick('/')}>Обо мне</MenuItem>
          <MenuItem onClick={() => handleMenuClick('/articles')}>
            Pet-проект
          </MenuItem>
          <MenuItem onClick={() => handleMenuClick('/admin')}>
            Контакты
          </MenuItem>
        </Menu>
        {matches ? (
          <IconButton
            edge='start'
            className={classes.menuButton}
            color='inherit'
            area-label='menu'
            onClick={handleClick}
          >
            <MenuIcon />
          </IconButton>
        ) : (
          <>
            <div className={classes.menuList}>
              <MenuItem exact to='/' component={NavLink}>
                Обо мне
              </MenuItem>
              <MenuItem to='/articles' component={NavLink}>
                Pet-проект
              </MenuItem>
              <MenuItem to='/admin' component={NavLink}>
                Контакты
              </MenuItem>
            </div>
          </>
        )}
      </Toolbar>
    </AppBar>
  )
}