import React, { useState } from 'react'
import { Typography, Accordion, AccordionSummary, AccordionDetails } from '@material-ui/core'
import { makeStyles } from '@material-ui/core/styles'
import ExpandMoreIcon from '@mui/icons-material/ExpandMore'

const useStyles = makeStyles((theme) => ({
  educationAc: {
    backgroundColor: '#f5f5d1'
  },
  educationTitle: {
    width: '33%',
    flexShrink: 0
  },
  educationDes: {
    color: '#9d9b9b;'
  }
}))

export const Education = () => {
  const classes = useStyles()
  const [expanded, setExpanded] = useState(false)

  const handleChange = (panel) => (event, isExpanded) => {
    setExpanded(isExpanded ? panel : false)
  }

  return (
    <div>
      <Accordion expanded={expanded === 'panel1'} onChange={handleChange('panel1')} className={classes.educationAc}>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1bh-content"
          id="panel1bh-header"
        >
          <Typography className={classes.educationTitle}>
            Frontend
          </Typography>
          <Typography className={classes.educationDes}>Web разработка</Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography>
            2021 год. JavaScript Junior Frontend Developer, создатель Владилен Минин.
          </Typography>
        </AccordionDetails>
      </Accordion>
      <Accordion expanded={expanded === 'panel2'} onChange={handleChange('panel2')} className={classes.educationAc}>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1bh-content"
          id="panel1bh-header"
        >
          <Typography className={classes.educationTitle}>
            УГАТУ
          </Typography>
          <Typography className={classes.educationDes}>Высшее образование</Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography>
            2004 год. Уфимский государственный авиационный технический университет, Уфа. ИРТ, Вычислительные машины, комплексы, системы и сети
          </Typography>
        </AccordionDetails>
      </Accordion>
    </div>
  )
}